@font-face {
  font-family: "Golos";
  src: url("../fonts/Golos-regular.ttf") format("ttf");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Golos";
  src: url("../fonts/Golos-medium.ttf") format("ttf");
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Golos";
  src: url("../fonts/Golos-bold.ttf") format("ttf");
  font-weight: 700;
  font-display: swap;
}
body {
  font-family: "Golos", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  padding: 0;
}

.container {
  width: 288px;
  padding: 0 16px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 624px;
    padding: 0 72px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 944px;
    padding: 0 40px;
  }
}

.main-header__intro-block {
  background-color: #f4f5f8;
}

.main-header__contact-block {
  display: flex;
  justify-content: space-between;
  height: 44px;
  padding: 12px 0 16px 0;
}

.main-header__logo {
  display: block;
  background-image: url("../img/icons/logo.svg");
  width: 119px;
  height: 28px;
  transition: 0.4s;
}
.main-header__logo:hover, .main-header__logo:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-header__contacts {
  display: flex;
  flex-direction: column;
}
.main-header__contacts a {
  text-decoration: none;
  color: #757575;
  transition: 0.4s;
}
.main-header__contacts a:hover, .main-header__contacts a:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-header__phone {
  margin-top: 8px;
}

.main-header__info {
  display: none;
}

.main-nav {
  position: relative;
}

.main-nav__menu-list {
  position: absolute;
  left: -16px;
  right: -16px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  margin: 0;
  padding: 80px 16px 0 16px;
  transform: translateY(-300%);
  list-style: none;
  transition: 0.8s;
  z-index: 10;
}
.main-nav__menu-list::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 16px;
  background-image: url("../img/icons/logo.svg");
  background-repeat: no-repeat;
  width: 119px;
  height: 28px;
}
.main-nav__menu-list--open {
  height: 85vh;
  transform: translateY(-17%);
  transition: 0.2s;
}
.main-nav__menu-list--open ~ .main-nav__button-block .main-nav__menu-button {
  position: absolute;
  top: -72px;
  right: 16px;
  background-image: url("../img/icons/close-menu.svg");
  z-index: 11;
  transition: 0.4s;
}
.main-nav__menu-list--open ~ .main-nav__button-block .main-nav__consultation {
  bottom: -349px;
  z-index: 11;
  transform: translateY(0);
}
.main-nav__menu-list--open ~ .main-nav__button-block .main-nav__consultation:hover, .main-nav__menu-list--open ~ .main-nav__button-block .main-nav__consultation:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-nav__link {
  color: #202226;
  text-decoration: none;
  display: flex;
  padding: 16px 0;
  border-bottom: 1px solid #e9e9e9;
  transition: 0.4s;
}
.main-nav__link:hover, .main-nav__link:focus {
  opacity: 0.7;
  transition: 0.4s;
  border-bottom: 1px solid #202226;
}

.main-nav__button-block {
  display: flex;
  margin-top: 16px;
  margin-bottom: 36px;
}

.main-nav__search {
  display: block;
  width: 44px;
  height: 44px;
  background-image: url("../img/icons/search.svg");
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.main-nav__search:hover, .main-nav__search:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-nav__basket {
  display: block;
  width: 44px;
  height: 44px;
  background-image: url("../img/icons/basket.svg");
  margin-left: 8px;
  margin-right: auto;
  transition: 0.4s;
}
.main-nav__basket:hover, .main-nav__basket:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-nav__menu-button {
  display: block;
  width: 44px;
  height: 44px;
  background-color: transparent;
  background-image: url("../img/icons/menu.svg");
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.main-nav__menu-button:hover {
  opacity: 0.7;
  transition: 0.4s;
}

.main-nav__consultation {
  color: #ffffff;
  text-decoration: none;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 288px;
  height: 44px;
  background-color: #202226;
  border-radius: 8px;
  transform: translateY(-300%);
  transition: 0.4s;
}

.main-footer {
  background-color: #f4f5f8;
  padding: 16px 0;
}

.main-footer__logo {
  display: block;
  background-image: url("../img/icons/logo.svg");
  width: 119px;
  height: 28px;
}

.main-footer__intro {
  margin-top: 16px;
  padding-top: 52px;
  border-top: 1px solid #e9e9e9;
}

.main-footer__contacts {
  font-size: 18px;
  line-height: 24px;
}

.main-footer__address {
  display: inline-block;
  width: 280px;
  margin-bottom: 32px;
}

.main-footer__contact-link {
  text-decoration: none;
  color: #202226;
  transition: 0.4s;
}
.main-footer__contact-link:hover, .main-footer__contact-link:focus {
  opacity: 0.7;
  transition: 0.4s;
}
.main-footer__contact-link--phone {
  display: inline-block;
  margin-bottom: 13px;
}

.main-footer__messengers {
  display: flex;
  column-gap: 8px;
  margin: 32px 0;
}

.main-footer__messenger-link {
  color: #ffffff;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 109px;
  height: 44px;
  background-color: #202226;
  border-radius: 8px;
  transition: 0.4s;
}
.main-footer__messenger-link:hover, .main-footer__messenger-link:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-footer__socials {
  display: flex;
}

.main-footer__social-link {
  display: block;
  width: 44px;
  height: 44px;
  background-repeat: no-repeat;
  transition: 0.4s;
}
.main-footer__social-link:hover, .main-footer__social-link:focus {
  opacity: 0.7;
  transition: 0.4s;
}
.main-footer__social-link--instagram {
  background-image: url("../img/icons/instagram.svg");
}
.main-footer__social-link--youtube {
  background-image: url("../img/icons/youtube.svg");
  margin-left: 8px;
  margin-right: 16px;
}

.main-footer__subscribe {
  color: #757575;
  width: 168px;
  margin-top: 6px;
}

.main-footer__pages {
  display: flex;
  flex-direction: column;
  row-gap: 52px;
  margin-top: 52px;
}

.main-footer__pages-info {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.main-footer__pages-title {
  font-size: 16px;
  line-height: 20px;
  margin-top: 0;
  margin-bottom: 4px;
}

.main-footer__pages-link {
  color: #202226;
  text-decoration: none;
  transition: 0.4s;
}
.main-footer__pages-link:hover, .main-footer__pages-link:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.main-footer__copyrights {
  color: #757575;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  margin-top: 52px;
  padding-top: 16px;
  border-top: 1px solid #e9e9e9;
}

.main-footer__confidentiality {
  color: #757575;
  text-decoration: none;
  transition: 0.4s;
}
.main-footer__confidentiality:hover, .main-footer__confidentiality:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.slider {
  background-color: #202226;
  padding: 12px;
  border-radius: 12px;
}

.slider__device-photo {
  display: block;
  background-color: #ffffff;
  border-radius: 8px;
  margin: 0 auto;
}

.slider__info {
  color: #757575;
  margin-top: 12px;
}

.slider__title {
  font-size: 24px;
  line-height: 28px;
  text-align: left;
  color: #ffffff;
  margin: 0;
}

.slider__options {
  font-size: 12px;
  margin-top: 12px;
}

.slider__option {
  display: flex;
  margin: 0;
  padding: 12px 0;
  border-bottom: 1px solid #36383c;
}
.slider__option span {
  margin-left: auto;
}

.slider__price {
  font-size: 18px;
  line-height: 24px;
  color: #ffffff;
  margin-top: 40px;
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #36383c;
}

.slider__other-block {
  display: flex;
  justify-content: space-between;
}

.slider__more {
  color: #161616;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 118px;
  height: 44px;
  background-color: #ffffff;
  border-radius: 8px;
  transition: 0.4s;
}
.slider__more:hover, .slider__more:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.slider__button {
  background-color: #ffffff;
  width: 44px;
  height: 44px;
  padding: 0;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.4s;
}
.slider__button--prev {
  background-image: url("../img/icons/arr-prev.svg");
}
.slider__button--next {
  background-image: url("../img/icons/arr-next.svg");
}
.slider__button:hover, .slider__button:focus {
  opacity: 0.7;
  transition: 0.4s;
}

.category {
  margin-top: 60px;
}

.category__title {
  font-size: 24px;
  line-height: 28px;
  margin: 0 0 12px 0;
}

.category__description {
  color: #757575;
  text-align: left;
  display: block;
  width: 230px;
}

.category__product-list {
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  margin: 32px 0 0 0;
  padding: 0;
  list-style: none;
}

.category__product-item {
  position: relative;
  width: 264px;
  height: 246px;
  background-color: #f4f5f8;
  border-radius: 12px;
  padding: 12px;
}
.category__product-item img {
  background-color: transparent;
}

.category__product-link {
  font-size: 18px;
  line-height: 24px;
  color: #202226;
  text-decoration: none;
  display: inline-block;
  width: 176px;
  transition: 0.4s;
}
.category__product-link::before {
  content: "";
  position: absolute;
  bottom: 12px;
  width: 264px;
  height: 186px;
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  transition: 0.4s;
  border-radius: 20px;
}
.category__product-link--cables::before {
  background-image: url("../img/mobile/bench.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--cables::before {
    background-image: url("../img/mobile/bench@2x.png");
  }
}
.category__product-link--separators::before {
  background-image: url("../img/mobile/separators.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--separators::before {
    background-image: url("../img/mobile/separators@2x.png");
  }
}
.category__product-link--heaters::before {
  background-image: url("../img/mobile/exchangers.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--heaters::before {
    background-image: url("../img/mobile/exchangers@2x.png");
  }
}
.category__product-link--radiators::before {
  background-image: url("../img/mobile/radiators.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--radiators::before {
    background-image: url("../img/mobile/radiators@2x.png");
  }
}
.category__product-link--spare::before {
  background-image: url("../img/mobile/spare.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--spare::before {
    background-image: url("../img/mobile/spare@2x.png");
  }
}
.category__product-link--other::before {
  background-image: url("../img/mobile/other.png");
}
@media (min-resolution: 2dppx) {
  .category__product-link--other::before {
    background-image: url("../img/mobile/other@2x.png");
  }
}
.category__product-link:hover, .category__product-link:focus {
  opacity: 0.7;
  transition: 0.4s;
}
.category__product-link:hover::before, .category__product-link:focus::before {
  transform: scale(1.05);
  transition: 0.4s;
}

.advantages {
  margin-top: 60px;
}

.advantages__intro {
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}

.advantages__advantage-list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin: 32px 0 0 0;
  padding: 0;
  list-style: none;
}

.advantages__advantage-item {
  position: relative;
  width: 232px;
  margin-left: 57px;
}
.advantages__advantage-item::before {
  content: "";
  width: 44px;
  height: 44px;
  position: absolute;
  left: -57px;
  background-repeat: no-repeat;
}
.advantages__advantage-item--feedback::before {
  background-image: url("../img/icons/feedback.svg");
}
.advantages__advantage-item--experience::before {
  background-image: url("../img/icons/experience.svg");
}
.advantages__advantage-item--spare::before {
  background-image: url("../img/icons/spare.svg");
}
.advantages__advantage-item--delivery::before {
  background-image: url("../img/icons/delivery.svg");
}
.advantages__advantage-item--rent::before {
  background-image: url("../img/icons/rent.svg");
}
.advantages__advantage-item--guarantee::before {
  background-image: url("../img/icons/guarantee.svg");
}

.advantages__title {
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 8px 0;
}

.advantages__description {
  color: #757575;
}

.partners {
  margin: 60px 0;
}

.partners__info-block {
  background-color: #f4f5f8;
  padding: 12px;
  border-radius: 12px;
}

.partners__title {
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}

.partners__description {
  color: #757575;
  margin: 12px 0 0 0;
}

.partners__partner-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 9px;
  row-gap: 8px;
  margin: 8px 0 0 0;
  padding: 0;
  list-style: none;
}

.partners__partner-item {
  width: 90px;
  height: 82px;
  background-color: #f4f5f8;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 12px;
}
.partners__partner-item--iif {
  background-image: url("../img/mobile/iif.png");
  background-size: 54px 54px;
}
@media (min-resolution: 2dppx) {
  .partners__partner-item--iif {
    background-image: url("../img/mobile/iif@2x.png");
  }
}
.partners__partner-item--moskabelmet {
  background-image: url("../img/mobile/moskabelmet.png");
  background-size: 57px 54px;
}
@media (min-resolution: 2dppx) {
  .partners__partner-item--moskabelmet {
    background-image: url("../img/mobile/moskabelmet@2x.png");
  }
}
.partners__partner-item--olimpic {
  background-image: url("../img/mobile/olimpic.png");
  background-size: 71px 74px;
}
@media (min-resolution: 2dppx) {
  .partners__partner-item--olimpic {
    background-image: url("../img/mobile/olimpic@2x.png");
  }
}
.partners__partner-item--techmix {
  background-image: url("../img/techmix.svg");
  background-size: 45px 54px;
}
.partners__partner-item--UMZ {
  background-image: url("../img/UMZ.svg");
  background-size: 70px 45px;
}
.partners__partner-item--metal {
  background-image: url("../img/mobile/metal.png");
  background-size: 68px 27px;
}
@media (min-resolution: 2dppx) {
  .partners__partner-item--metal {
    background-image: url("../img/mobile/metal@2x.png");
  }
}

.feedback {
  background-color: #202226;
  padding: 12px;
  border-radius: 12px;
  margin-bottom: 60px;
}

.feedback__title {
  color: #ffffff;
  margin: 0 0 12px 0;
}

.feedback__description {
  color: #757575;
}

.feedback__input-zone {
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  margin: 32px 0 0 0;
  padding: 0;
  border: none;
}
.feedback__input-zone input {
  background-color: transparent;
  padding: 15px 0;
  border: none;
  border-bottom: 2px solid #ffffff;
}
.feedback__input-zone input::placeholder {
  font-size: 14px;
  line-height: 16px;
  color: #9f9f9f;
}

.feedback__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  width: 114px;
  height: 44px;
  margin-top: 32px;
  padding: 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.4s;
}
.feedback__submit:hover, .feedback__submit:focus {
  opacity: 0.7;
  transition: 0.4s;
}